/* LAYOUT */
.header {
  background-image: linear-gradient(#3f3f3fc9, rgba(0, 0, 0, 0.1)), url(/build/img/fondo.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 40rem; }

.apartados-header {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  align-items: flex-start; }

.enlace-login {
  font-size: 2.5rem;
  font-family: "Caveat", cursive;
  font-weight: 600;
  margin: auto; }

.enlace-icono {
  margin-top: 3rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-family: "Lobster", cursive;
  color: white;
  font-size: 8rem; }
  .enlace-icono img {
    height: 15rem;
    width: 15rem; }

a, p {
  color: white;
  font-size: 4rem;
  font-weight: 600;
  font-family: "Caveat", cursive;
  padding: 0; }
  a:hover, p:hover {
    color: #008cff; }

.div-enlaces {
  display: flex;
  align-self: center;
  align-items: flex-start; }
  .div-enlaces a {
    margin-left: 3rem; }

footer {
  display: flex;
  flex-direction: row;
  background-color: #353434;
  margin-bottom: 0;
  height: 10rem; }
  footer a, footer p {
    color: white; }
  footer p {
    padding-left: 20rem; }

.layout-menu {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center; }

.contenedor-menu {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  margin: 2rem; }

.noticia {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .noticia img {
    height: 17rem;
    width: 28rem;
    object-fit: cover; }
  .noticia a {
    font-family: "Anton", sans-serif;
    font-size: 2rem; }

.cont-list .titulo-encima {
  position: none;
  color: black;
  top: auto; }
  .cont-list .titulo-encima:hover {
    color: #008cff; }

.cont-list .noticia {
  height: 35rem; }

.contenedor-menu.tv {
  display: grid;
  gap: 0.7rem;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center; }
  .contenedor-menu.tv img {
    border-bottom: none;
    height: 42rem;
    width: 30rem; }

.contenedor-tv {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  margin: 2rem; }
  .contenedor-tv img {
    height: 80rem;
    width: 56rem;
    object-fit: cover; }
  .contenedor-tv p {
    font-size: 2.3rem; }
  .contenedor-tv h3 {
    margin: -3rem 0 -2rem 0; }

.formulario fieldset {
  display: flex;
  flex-direction: column;
  border-radius: 1rem;
  width: 50rem;
  margin-right: 3rem; }
  .formulario fieldset legend, .formulario fieldset label {
    font-size: 4rem;
    font-family: "Rubik", sans-serif; }
  .formulario fieldset label {
    font-size: 2.5rem; }
  .formulario fieldset input {
    border: 1px solid black;
    border-radius: 1rem;
    height: 4rem; }
    .formulario fieldset input:last-of-type {
      margin-bottom: 2rem; }

.contenedor-login h1 {
  font-size: 4rem;
  font-family: "Caveat", cursive;
  font-weight: 600; }

.div-login {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 15rem; }
  .div-login h2 {
    color: white; }
  .div-login .img-login {
    max-width: 30rem; }

.contenido-noticia {
  margin: 0 4rem; }
  .contenido-noticia h1 {
    font-family: "Anton", sans-serif;
    font-size: 5rem; }
  .contenido-noticia .imagen2 {
    height: 55rem;
    width: 85rem;
    margin-left: auto;
    margin-right: auto;
    object-fit: cover; }
  .contenido-noticia p {
    font-weight: 600;
    text-align: justify;
    font-family: "Caveat", cursive;
    color: black; }

.div-desc2 {
  max-width: 85rem;
  margin-left: auto;
  margin-right: auto; }

.edicion {
