.txt_desc {
  height: 16rem;
  white-space: pre-line; }

fieldset {
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  border-radius: 1rem; }
  fieldset legend {
    margin: auto;
    font-family: "Rubik", sans-serif;
