.div-form select {
  font-family: "Rubik", sans-serif;
  font-weight: 600;
  padding-right: 2rem;
  margin-right: 4rem; }

.imagen-small {
  width: 40rem; }

.main-index h2 {
  font-family: "Rubik", sans-serif;
  font-weight: 600; }
